import React from 'react'
import Tabs from '../Tabs'
import '../style'
const TabPane = <div></div>
export default ()=>{
    function callback(key) {
        console.log(key);
    }
    return(
        <Tabs defaultActiveKey='2' onChange={callback} centered>
            <TabPane key='1' tab=''><img src="" alt="" /></TabPane>
            <TabPane key='2' tab=''>1111</TabPane>
            <TabPane key='3' tab=''>3333</TabPane>
        </Tabs>
    )
}